.middle{
  /*display: inline;*/
  background-color: #999;
  padding:4px;
  /*float:left;*/
  clear:both;
  /*width: 20%;*/
  margin:auto;
  margin-bottom:8px;
  margin-top:5px;

}


.middle h5{
  display:inline;
  margin:auto;
  padding:10px;
}

.middle h5:hover{
  cursor: pointer;
}
.topleft{
  border-top-left-radius:100%;
}

.topright{
  border-top-right-radius: 100%;
}

.bottomleft {
  border-bottom-left-radius: 100%;
}

.bottomright{
  border-bottom-right-radius: 100%;
}

.red{
  background-color: #c90202;
}

.blue{
  background-color: #0620ef;
}

.green{
  background-color: #0b1;
}

.yellow{
  background-color: #ee0;
}

